import React from "react";

// Components
import Note from "components/Note/Note";

// Images
import insuranceCardExample from "images/insuranceCard.jpg";

// Material
import {
  Button,
  Card,
  CardContent,
  FormControl,
  FormControlLabel,
  FormLabel,
  InputLabel,
  MenuItem,
  Radio,
  RadioGroup,
  Select,
  Step,
  StepLabel,
  Stepper,
  TextField,
  Typography
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles(theme => ({
  field: {
    marginBottom: theme.spacing(3)
  },
  genderFields: {
    alignItems: "center",
    display: "flex",
    flexDirection: "row",
    flexWrap: "wrap",
    marginBottom: theme.spacing(3)
  },
  genderLabel: {
    marginRight: theme.spacing(5)
  },
  maleFemale: {
    flexDirection: "row"
  },
  insuranceCard: {
    display: "flex",
    justifyContent: "center",
    marginBottom: theme.spacing(3)
  }
}));

const GenderSex = () => {
  const classes = useStyles();

  const [value, setValue] = React.useState("male");

  const handleChange = event => {
    setValue(event.target.value);
  };

  return (
    <FormControl className={classes.genderFields}>
      <FormLabel className={classes.genderLabel} component="legend">
        Sex
      </FormLabel>
      <RadioGroup
        aria-label="gender"
        className={classes.maleFemale}
        name="gender1"
        value={value}
        onChange={handleChange}
      >
        <FormControlLabel value="male" control={<Radio />} label="Male" />
        <FormControlLabel value="female" control={<Radio />} label="Female" />
      </RadioGroup>
    </FormControl>
  );
};

const WhoFor = () => {
  const classes = useStyles();

  const [age, setAge] = React.useState("");

  const handleChange = event => {
    setAge(event.target.value);
  };

  return (
    <Card>
      <CardContent>
        <form noValidate autoComplete="off">
          <p>Who is this claim for?</p>
          <FormControl fullWidth className={classes.field}>
            <Select
              value={age}
              onChange={handleChange}
              displayEmpty
              variant="outlined"
            >
              <MenuItem value="">Omar Allwell</MenuItem>
              <MenuItem value={10}>Karen Allwell</MenuItem>
              <MenuItem value={20}>Renee Allwell</MenuItem>
              <MenuItem value={30}>-=New person=-</MenuItem>
            </Select>
          </FormControl>

          <div className={classes.insuranceCard}>
            <img alt="Insurance card" src={insuranceCardExample} />
          </div>

          <GenderSex />

          <TextField
            className={classes.field}
            defaultValue="andrewjohnson@example.com"
            fullWidth
            label="Email"
            variant="outlined"
          />
          <TextField
            className={classes.field}
            fullWidth
            defaultValue="17 June 1990"
            label="Date of birth"
            variant="outlined"
          />

          <TextField
            className={classes.field}
            fullWidth
            defaultValue="0555723123"
            label="Mobile number"
            variant="outlined"
          />
          <Note status="hide">
            Numbers only - for future, we should provide a mask
          </Note>

          <TextField
            className={classes.field}
            defaultValue="USA"
            fullWidth
            label="Nationality"
            variant="outlined"
          />
        </form>
      </CardContent>
    </Card>
  );
};

export default WhoFor;
