import React from "react";
import { Route } from "react-router-dom";

// Components
import BankDetails from "components/NewClaim/BankDetails"
import Home from "components/Home";
import Note from "components/Note/Note";
import ReviewAndSubmit from "components/NewClaim/ReviewAndSubmit";
import SupportingDocuments from "components/NewClaim/SupportingDocuments";
import TreatmentDetails from "components/NewClaim/TreatmentDetails";
import WhoFor from "components/NewClaim/WhoFor";

// Material
import { makeStyles } from "@material-ui/core/styles";
import {
  Button,
  FormControl,
  FormControlLabel,
  FormLabel,
  InputLabel,
  MenuItem,
  Radio,
  RadioGroup,
  Select,
  Step,
  StepLabel,
  Stepper,
  TextField,
  Typography
} from "@material-ui/core";

const useStyles = makeStyles(theme => ({
  root: {
    width: "100%"
  },
  backButton: {
    marginRight: theme.spacing(1)
  },
  instructions: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1)
  },
  form: {
    root: {
      "& > *": {
        margin: theme.spacing(1)
      }
    }
  }
}));

function getSteps() {
  return [
    "Who is this claim for?",
    "Treatment",
    "Supporting documents",
    "Bank details",
    "Review & Submit"
  ];
}

const NewClaim = () => {
  const classes = useStyles();

  // Steppers
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();

  const handleNext = () => {
    setActiveStep(prevActiveStep => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep(prevActiveStep => prevActiveStep - 1);
  };

  // const handleReset = () => {
  //   setActiveStep(0);
  // };

  const getStepContent = stepIndex => {
    switch (stepIndex) {
      case 0:
        return <WhoFor />;
      case 1:
        return <TreatmentDetails />;
      case 2:
        return <SupportingDocuments />;
      case 3:
        return <BankDetails />;
      case 4:
        return <ReviewAndSubmit />;
      default:
        return "Unknown stepIndex";
    }
  };

  return (
    <div className={classes.root}>
      <div>
        {activeStep === steps.length ? (
          <div>
            <Home />
            {/* <Button onClick={handleReset}>Reset</Button> */}
          </div>
        ) : (
          <>
            <h1>New claim</h1>
            <Stepper activeStep={activeStep} alternativeLabel>
              {steps.map(label => (
                <Step key={label}>
                  <StepLabel>{label}</StepLabel>
                </Step>
              ))}
            </Stepper>
            <div>
              <div className={classes.instructions}>
                {getStepContent(activeStep)}
              </div>
              <div>
                <Button
                  disabled={activeStep === 0}
                  onClick={handleBack}
                  className={classes.backButton}
                >
                  Back
                </Button>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleNext}
                >
                  {activeStep === steps.length - 1 ? "Submit claim" : "Next"}
                </Button>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default NewClaim;
