// Notes can use the status props - show/hide/done. Default is show

import React from "react";

// CSS
import "./Note.css";

const Note = props => {
  const status = props.status;
  console.log("status: " + status);
  if (status === "hide") {
    console.log("message: " + props.children);
  }

  return <span className={status ? status : "show"}>{props.children}</span>;
};

export default Note;
