import React from "react";

// Components
import Note from "components/Note/Note";

// Material
import { makeStyles } from "@material-ui/core/styles";
import {
  Button,
  Step,
  StepLabel,
  Stepper,
  TextField,
  Typography
} from "@material-ui/core";

const useStyles = makeStyles(theme => ({
  root: {
    width: "100%"
  },
  backButton: {
    marginRight: theme.spacing(1)
  },
  instructions: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1)
  },
  form: {
    root: {
      "& > *": {
        margin: theme.spacing(1),
        width: 200
      }
    },
    MuiTextField: {
      width: "600px"
    }
  }
}));

function getSteps() {
  return ["Treatment", "Supporting documents", "Review & Submit", "Summary"];
}

const NewClaim = () => {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();

  const handleNext = () => {
    setActiveStep(prevActiveStep => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep(prevActiveStep => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  const step1 = (
    <form noValidate autoComplete="off" className={classes.form.root}>
      <TextField label="When did the treatment start?" variant="outlined" />
      <TextField
        className={classes.form.MuiTextField}
        label="When did the treatment finish?"
        variant="outlined"
      />
      <TextField
        label="What is the name of the hospital or clinic you attended?"
        variant="outlined"
      />
      <TextField
        label="What country was this in?"
        variant="outlined"
      />
      <Note>Dima: How about setting the default to UAE</Note>
      <TextField
        label="Has the doctor written an order to admit you to a hospital?"
        variant="outlined"
      />
      <Note>
        If the doctor admits the patient to hospital, they are an inpatient.
        Otherwise they are an outpatient
      </Note>
    </form>
  );

  const step2 = (
    <form>
      <p>To make a successful claim, please upload either scans or photographs of the items below</p>
      <h3>Health Claim form</h3>
      <p>
        Please upload the Health Claim form provided by your hospital or clinic
      </p>
      <p>It should look like the image below</p>
      <p>[Image of form]</p>
      <Button color="primary" variant="outlined">
        Upload file
      </Button>

      <h3>Healthcare provider bills</h3>
      <p>Do you have any invoices from the hospital or clinic?</p>
      <Button color="primary" variant="outlined">
        Upload files
      </Button>
      <p>Do you have any receipts from the hospital or clinic?</p>
      <Button color="primary" variant="outlined">
        Upload files{" "}
        <Note status="hide">Don't forget to ask for claim amount</Note>
      </Button>

      <h3>Prescription and pharmacy bills</h3>
      <p>
        Do you have any invoices related to medical prescriptions or pharmacy
        purchases?
      </p>
      <Button color="primary" variant="outlined">
        Upload files
      </Button>
      <p>
        Do you have any receipts for medical prescriptions and pharmacy
        purchases?
      </p>
      <Button color="primary" variant="outlined">
        Upload files{" "}
        <Note status="hide">Don't forget to ask for claim amount</Note>
      </Button>

      <h3>Medical Reports</h3>
      <p>
        Please upload any medical reports, lab tests, or diagnostics results
        related to this claim
      </p>
      <Button color="primary" variant="outlined">
        Upload files{" "}
        <Note status="hide">Don't forget to ask for claim amount</Note>
      </Button>

      <h3>In-patient discharge summary and itemised bill</h3>
      <p>Please upload the discharge summary provided by the hospital or clinic</p>
      <p>This may also be known as an "after visit summary"</p>
      <Button color="primary" variant="outlined">
        Upload file
      </Button>
      <p>Please upload the itemised bill provided by the hospital or clinic</p>
      <p>This may also be known as a "detailed bill"</p>
      <Button color="primary" variant="outlined">
        Upload file
      </Button>
    </form>
  );

  const getStepContent = stepIndex => {
    switch (stepIndex) {
      case 0:
        return step1;
      case 1:
        return step2;
      case 2:
        return "This is the bit I really care about!";
      case 3:
        return "This is the bit I really care about!";
      default:
        return "Unknown stepIndex";
    }
  };

  return (
    <div className={classes.root}>
      <h1>New claim</h1>
      <Stepper activeStep={activeStep} alternativeLabel>
        {steps.map(label => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>
      <div>
        {activeStep === steps.length ? (
          <div>
            <Typography className={classes.instructions}>
              All steps completed
            </Typography>
            {/* <Button onClick={handleReset}>Reset</Button> */}
          </div>
        ) : (
          <div>
            <div className={classes.instructions}>
              {getStepContent(activeStep)}
            </div>
            <div>
              <Button
                disabled={activeStep === 0}
                onClick={handleBack}
                className={classes.backButton}
              >
                Back
              </Button>
              <Button variant="contained" color="primary" onClick={handleNext}>
                {activeStep === steps.length - 1 ? "Finish" : "Next"}
              </Button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default NewClaim;
