import React from "react";
import { Helmet } from "react-helmet";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

// Components
import AllFields from "components/AllFields";
import Home from "components/Home";
import HomeEmpty from "components/HomeEmpty";
import Nav from "components/Nav";
import NewClaim from "components/NewClaim";
import NewClaimDependents from "components/NewClaim/NewClaimDependents";
import Shop from "components/Shop";


// CSS
import "./App.css";

// Material
import CssBaseline from "@material-ui/core/CssBaseline";
import { ThemeProvider } from "@material-ui/core/styles";

// Themeing
import BayzatTheme from "themes/BayzatTheme";

function App() {
  const PageTitle = "Bayzat - Claims";

  return (
    <>
      <ThemeProvider theme={BayzatTheme}>
        <CssBaseline />
        <Router basename={process.env.PUBLIC_URL}>
          <div className="App">
            <Helmet>
              <title>{PageTitle}</title>
              <link
                rel="icon"
                type="image/svg+xml"
                href="./images/favicon.jpg"
              />
              <link 
              rel="stylesheet" 
              type="text/css" 
              href="./fonts/fonts.css" />
            </Helmet>
            <Nav />
            <Switch>
              <Route path="/" exact component={HomeEmpty} />
              <Route path="/allFields" component={AllFields} />
              <Route path="/shop" exact component={Shop} />
              <Route path="/newClaim" exact component={NewClaim} />
              <Route path="/newClaimDependents" exact component={NewClaimDependents} />
              <Route path="/claims" exact component={Home} />
            </Switch>
          </div>
        </Router>
      </ThemeProvider>
    </>
  );
}

export default App;
